import { Component, OnInit } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { IOptionsPsInputNumericExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-numeric/ps-input-numeric.component.interfaces';
import { CommonBussinessConstant } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { IOptionsPsContainerPanel, IOptionsPsKeyinInput, IOptionsTemplateStepper } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { OmniBasePage } from '../omni-base/omni-base.page';
import { OmniPullService } from './../../commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { LoggerService } from './../../commonSRC/psServices/logger/logger.service';

@Component({
  selector: 'token-management',
  templateUrl: './token-management.page.html',
  styleUrls: ['./token-management.page.scss'],
})
export class TokenManagementPage extends OmniBasePage implements OnInit {
  public formGroup: FormGroup = new FormGroup({});
  public tokenManagementVO: any = {};
  isDeactivate = false;
  isActivate = false;

  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'token_management_key',
    isHorizontalStepper: true,
    numberOfSteps: 1,
    namesofSteps: ['token_management_key'],
    group: this.formGroup,
    submitOptions: {
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.tokenManagementEndPoint,
      group: this.formGroup,
    },
    requestObject: this.tokenManagementVO,
  };

  hardTokenPanelOptions: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'hard_token_details_key',
    expanded: true
  };

  tokenSerialNumberOptions: IOptionsPsInputNumericExposed = {
    fcName: 'tokenSerialNo',
    group: this.formGroup,
    labelKey: 'token_serial_number_key',
    placeHolder: 'token_serial_number_key'
  };

  deactivateReasonOptions: IOptionsPsKeyinInput = {
    group: this.formGroup,
    fcName: 'deactivateReason',
    labelKey: 'deactivation_reason_key',
    placeHolder: 'enter_deactivation_reason_key'
  };


  constructor(private omniPull: OmniPullService, private logger: LoggerService, private navService: PsNavigatorService) {
    super();
  }

  ngOnInit() {
    super.init();
    PsCommonSettings.oper_ID = CommonBussinessConstant.TOKEN_MANAGEMENT_OPER_ID;
    this.tokenManagementVO = JSON.stringify(this.navService.getAllParams()) !== '{}' ? this.navService.getAllParams() : this.tokenManagementVO;
    this.isActivate = this.tokenManagementVO.optionType === 'activation';//PsCommonSettings.oper_ID === PsCommonBusinessSettings.TOKEN_ACTIVATION_OPER_ID;
    this.isDeactivate = this.tokenManagementVO.optionType === 'deactivation';//PsCommonSettings.oper_ID === PsCommonBusinessSettings.TOKEN_DEACTIVATION_OPER_ID;
  }

}
